$general_bg: #fcf7ef;
$general_fg: #242521;
$modeline_in_bg: #e5e3d8;
$modeline_in_fg: #59786f;
$modeline_ac_bg: #c0df6f;
$modeline_ac_fg: #142010;
$cursor_bg: #007f00;
$cursor_fg: #fcf7ef;

.Ymacs-Theme-ef-cyprus { --ymacs-cursor-bg: #007f00; --ymacs-cursor-fg: #fcf7ef; }
.Ymacs-Theme-ef-cyprus { color: #242521; background-color: #fcf7ef; }
.Ymacs-Theme-ef-cyprus .Ymacs_SplitCont > .bar { background-color: #969896; }
.Ymacs-Theme-ef-cyprus .Ymacs_SplitCont.horiz > .bar { border-top: 1px solid #f8f3ea; border-bottom: 1px solid #f8f3ea; }
.Ymacs-Theme-ef-cyprus .Ymacs_SplitCont.vert > .bar { border-left: 1px solid #f8f3ea; border-right: 1px solid #f8f3ea; }
.Ymacs-Theme-ef-cyprus .Ymacs_Frame-active .Ymacs-current-line { background-color: #f0e0d463 }
.Ymacs-Theme-ef-cyprus .Ymacs-frame-content div.line:before { color: #59786f; }
.Ymacs-Theme-ef-cyprus .Ymacs_Frame-active div.line.Ymacs-current-line:before { color: #000000; font-weight: bold; }
.Ymacs-Theme-ef-cyprus .Ymacs-frame-content:before { color: #59786f; background-color: #fcf7ef; }
.Ymacs-Theme-ef-cyprus {
.Ymacs_Popup {
  --ymacs-popup-bg: #f8f3ea;
  --ymacs-popup-fg: #242521;
  --ymacs-popup-scrollbar-thumb: var(--ymacs-popup-fg);
}
.Ymacs_Popup .Ymacs_Menu {
.Ymacs_Menu_Item:hover, .Ymacs_Menu_Item.selected, .Ymacs_Menu_Item.selected:hover { background-color: #e6e4bf; }
}
.Ymacs_Overlay.selection div { background-color: #e0e7e5; }
.Ymacs_Overlay.isearch div { color: #000000; background-color: #efbf00; }
.Ymacs_Overlay.isearch-lazy div { color: #000000; background-color: #cfceff; }
.Ymacs_Overlay.match-paren div { color: #000000; background-color: #32cd32; }
.Ymacs_Modeline { color: #59786f; background-color: #e5e3d8; }
.Ymacs_Frame-active .Ymacs_Modeline { color: #142010; background-color: #c0df6f; }
.minibuffer-prompt { color: #006f00; }
.mode-line-buffer-id { font-weight: bold; }
.directive { color: #a2604f; }
.type { color: #444fcf; }
.builtin { color: #557400; font-weight: bold; }
.function-name { color: #a7601f; }
.variable-name { color: #007a9f; }
.constant { color: #00824f; }
.string { color: #ca3400; }
.string-starter { color: #ca3400; }
.string-stopper { color: #ca3400; }
.regexp { color: #ca3400; }
.regexp-starter { color: #ca3400; }
.regexp-stopper { color: #ca3400; }
.regexp-modifier { color: #ca3400; }
.keyword { color: #006f00; font-weight: bold; }
.comment { color: #8f6f4a; font-style: italic; }
.mcomment { color: #8f6f4a; font-style: italic; }
.comment-starter { color: #8f6f4a; font-style: italic; }
.mcomment-starter { color: #8f6f4a; font-style: italic; }
.mcomment-stopper { color: #8f6f4a; font-style: italic; }
.number { color: #00824f; }
.operator { }
.error { color: #a7601f; }
.isearch-fail { color: #dd0020; background-color: #ffd4bf; font-weight: bold; }
.xml-open-tag { color: #a7601f; }
.xml-close-tag { color: #a7601f; }
.xml-attribute { color: #007a9f; }
.xml-entity-starter { color: #00824f; }
.xml-entity-stopper { color: #00824f; }
.xml-entity { color: #00824f; }
.xml-open-bracket { }
.xml-close-bracket { }
.xml-closetag-slash { }
.xml-cdata { color: #8f6f4a; font-style: italic; }
.xml-cdata-starter { color: #8f6f4a; font-style: italic; }
.xml-cdata-stopper { color: #8f6f4a; font-style: italic; }
.lisp-keyword { color: #557400; font-weight: bold; }
.markdown-heading1 { color: #557400; font-weight: bold; }
.markdown-heading2 { color: #bf4400; font-weight: bold; }
.markdown-heading3 { color: #3f6faf; font-weight: bold; }
.markdown-heading4 { color: #00824f; font-weight: bold; }
.markdown-heading5 { font-weight: bold; font-style: italic; }
.markdown-heading6 { font-weight: bold; }
.markdown-blockquote { color: #8f6f4a; font-style: italic; }
.markdown-blockquote1 { color: #8f6f4a; font-style: italic; }
.markdown-blockquote2 { color: #8f6f4a; font-style: italic; }
.markdown-blockquote3 { color: #8f6f4a; font-style: italic; }
}
